.ModalCapa {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 200;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.ModalContainer {
  background-color: white;
  padding: 10px;
  border-radius: 10px;
}
.ModalFunction {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 15px 10px;
}
.ModalFunction h2 {
  margin: 0;
}
.close-btn {
  padding: 0;
  margin: 0;
  border: none;
  right: 100px;
  height: 1.6em;
  width: 1.6em;
  text-align: center;
  border-radius: 50%;
  background-color: white;
  color: rgb(70, 70, 70);
  font-weight: 900;
  font-size: 1.5em;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transition: background-color 300ms;
  -o-transition: background-color 300ms;
  transition: background-color 300ms;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.close-btn:hover {
  background-color: rgb(182, 182, 182);
}



